import React from "react";

class TrData extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            this.items.map((item) => {
                const name = item.formComponentValues[0].name;
                const result = item.result;
                const approveUserid = item.approverUserids[0];
                return (
                    <tr className="text-center">
                        <td>{name}</td>
                        <td>{result}</td>
                        <td>{approveUserid}</td>
                    </tr>
                )
            })
        )
    }
}

class List extends React.Component {


    items = [];
    isLoaded;

    constructor(props, isLoaded) {
        super(props);
        this.isLoaded = isLoaded;
        this.items = props;
        this.render();
    }

    render() {
        if (!this.isLoaded) {

        } else
            return (
                <table className="table table-bordered">
                    <thead>
                    <tr>
                        <th>物品用途</th>
                        <th>审批状态</th>
                        <th>审批人</th>
                    </tr>
                    </thead>
                    <tbody>
                    <TrData />
                    </tbody>
                </table>
            )
    }
}

export default List;
